
.contenido-header{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    @include tablet{
        flex-direction: row;
        
                      
    }
      
}

.navegacion{

        
    display:flex;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .2s ease-in-out, opacity .5s linear, height .1s linear;
    
    @include tablet{
        gap:5rem;
        visibility: visible;
        opacity: 1;
    
    }

    a{
        color:$negro;
        font-size: 2rem;
        font-weight: $regular;
        &:hover{
            color:$marronOscuro;
        }
    }

    img{
        height: 6rem;
        width: 6rem;
        
    }
}

.boton-barra{
    visibility: visible;
    margin-bottom: 1rem;
    @include tablet{
        visibility: hidden;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
    flex-direction: column;
    gap: 3rem;
    
    a{
        color:$negro;
        font-size: 1.5rem;
    }
    
}
