@mixin telefono{
    @media(min-width: $telefono){
        @content;
    }
}

@mixin tablet{
    @media(min-width: $tablet){
        @content;
    }
}

@mixin desktop{
    @media(min-width: $desktop){
        @content;
    }
}

@mixin grid ($columnas, $espaciado){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin boton($bckColor, $display){
    background-color: $bckColor;
    color:$blanco;
    text-decoration: none;
    padding:1rem 3rem;
    text-align: center;
    margin-top: $separacion/2;
    display: $display;
    border:none;

    &:hover{
        background-color: darken($bckColor,5);
        cursor:pointer;
    }
}