.hero{
    background-image: url(../../build/img/hero.webp);
    
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%,50%;
    height:50rem;
    @include tablet{
        height: 80rem;
    }
    //position:relative;
    //margin-bottom: 2rem

    .contenido-hero{
        //position: absolute;
        background-color: rgb(0 0 0 / 30%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: $separacion*2;
    }

    h2{
        text-align: center;
        color:$blanco;
        @include tablet{
            font-size: 8rem;
        }
        font-size: 3rem;
    }
}