.formulario{
    margin-top: 1.8rem;
    p{
        font-size:1.4rem;
        color:$negro;
        margin: 2rem 0 0 0;
    }

    legend{
        font-size: 2rem;
        color:$grisOscuro;
        margin-bottom: 1.5rem;
    }

    label{
        font-weight: $bold;
        text-transform: uppercase;
        display:block;
        margin-bottom: 1.5rem;
    }

    input:not([type="submit"]),textarea,select{
        padding: 1rem;
        display:block;
        width: 100%;
        background-color:$blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
        background-color: $amarillo;
    }

    textarea{
        height: 20rem;
    }

    .forma-contacto{
        display:flex;
        justify-content: space-between;
        align-items:center;
        max-width: 30rem;
    }

    input[type="radio"]{
        margin:0;
        width: auto;
        
    }

}

