.servicios{
    padding: $separacion/2;
    margin-top: $separacion;
    @include tablet{
        margin-top: $separacion*1.5;
    }

    background-color: $blanco;
    h2{
        text-align: center;
    }

    p{
        font-weight: $delgada;
        line-height: 3rem;
        font-size: 1.8rem;
        @include tablet{
            font-size:2rem;
        }
        text-align: center;
    }
}

.seccion-servicios{

    margin-top: 10rem;
    @include tablet{
        @include grid(3,$separacion);
    }
    p{
        font-size: 1.7rem;
        //background-color: red;
    }
}

.tarjeta{
    margin-bottom: 5rem;
    @include tablet{
        margin-bottom: 0;
    }
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $beige;
    border-radius: 4rem;
    h3{
        text-align: center;
    }
    
}

.logos{
    margin-top: 8rem;
    display: flex;
    justify-content: center;
    gap:8rem;
    
        
    
}