#map {
    border: 1px solid $gris;
    border-radius: 2px;
    height: 30rem;
    width: 30rem;
    @include tablet{
        height: 40rem;
        width: 40rem;
    }  
}

.contacto{
    margin-top: 5rem;
    @include tablet{
        display:grid;
        grid-template-columns: 1fr 3fr;
        grid-auto-flow: column;
        gap:8rem;
        
    }

}

.icono{
    padding: 2rem;
    display: inline-block;
    background-color: $beige;
    border-radius: 2rem;
}

.tarjeta-contacto{
    
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @include tablet{
        display:flex;
        flex-direction: row;
        gap:2rem;
        align-items: center;
        margin-top: 0;
    }       
}

.contacto-titulo{
    margin-top: 2rem;
    font-size: 1.4rem;
    text-align: center;
    @include tablet{
        font-size: 1.6rem;
        margin-top:0;
    }
 
    font-weight: $bold;
    color:$grisOscuro;
}

.contacto-iconos{
    @include tablet{
        display:flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
}