html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  background-color: #f1f1f1;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 180rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.bg-blanco {
  background-color: #fff;
}

.bg-amarillo {
  background-color: #FEF46D;
}

.sombra {
  -webkit-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  -moz-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  background-color: var(--blanco);
  padding: 2rem;
  border-radius: 1rem;
}

.boton-celeste {
  background-color: #8FBDD3;
  color: #fff;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-celeste:hover {
  background-color: #7db2cc;
  cursor: pointer;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .contenido-header {
    flex-direction: row;
  }
}

.navegacion {
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.2s ease-in-out, opacity 0.5s linear, height 0.1s linear;
}
@media (min-width: 768px) {
  .navegacion {
    gap: 5rem;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a {
  color: #000;
  font-size: 2rem;
  font-weight: 400;
}
.navegacion a:hover {
  color: #A97155;
}
.navegacion img {
  height: 6rem;
  width: 6rem;
}

.boton-barra {
  visibility: visible;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .boton-barra {
    visibility: hidden;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
  flex-direction: column;
  gap: 3rem;
}
.mostrar a {
  color: #000;
  font-size: 1.5rem;
}

.servicios {
  padding: 2.5rem;
  margin-top: 5rem;
  background-color: #fff;
}
@media (min-width: 768px) {
  .servicios {
    margin-top: 7.5rem;
  }
}
.servicios h2 {
  text-align: center;
}
.servicios p {
  font-weight: 300;
  line-height: 3rem;
  font-size: 1.8rem;
  text-align: center;
}
@media (min-width: 768px) {
  .servicios p {
    font-size: 2rem;
  }
}

.seccion-servicios {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .seccion-servicios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.seccion-servicios p {
  font-size: 1.7rem;
}

.tarjeta {
  margin-bottom: 5rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #E4D1B9;
  border-radius: 4rem;
}
@media (min-width: 768px) {
  .tarjeta {
    margin-bottom: 0;
  }
}
.tarjeta h3 {
  text-align: center;
}

.logos {
  margin-top: 8rem;
  display: flex;
  justify-content: center;
  gap: 8rem;
}

.hero {
  background-image: url(../../build/img/hero.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%, 50%;
  height: 50rem;
}
@media (min-width: 768px) {
  .hero {
    height: 80rem;
  }
}
.hero .contenido-hero {
  background-color: rgba(0, 0, 0, 0.3);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10rem;
}
.hero h2 {
  text-align: center;
  color: #fff;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .hero h2 {
    font-size: 8rem;
  }
}

.formulario {
  margin-top: 1.8rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #000;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
  margin-bottom: 1.5rem;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  margin-bottom: 1.5rem;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #fff;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
  background-color: #FEF46D;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 30rem;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.footer {
  margin-top: 7.5rem;
}

#map {
  border: 1px solid #e1e1e1;
  border-radius: 2px;
  height: 30rem;
  width: 30rem;
}
@media (min-width: 768px) {
  #map {
    height: 40rem;
    width: 40rem;
  }
}

.contacto {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contacto {
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-auto-flow: column;
    gap: 8rem;
  }
}

.icono {
  padding: 2rem;
  display: inline-block;
  background-color: #E4D1B9;
  border-radius: 2rem;
}

.tarjeta-contacto {
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .tarjeta-contacto {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    align-items: center;
    margin-top: 0;
  }
}

.contacto-titulo {
  margin-top: 2rem;
  font-size: 1.4rem;
  text-align: center;
  font-weight: 700;
  color: #333333;
}
@media (min-width: 768px) {
  .contacto-titulo {
    font-size: 1.6rem;
    margin-top: 0;
  }
}

@media (min-width: 768px) {
  .contacto-iconos {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
}